.header {
  padding: 0px 15px 0px 15px;
  display: flex;
  justify-content: space-between;
  position: sticky;
  background-color: white;
  max-width: 100%;
  z-index: 100;
  top: 0;
  box-shadow: 0px 5px 8px -9px rgba(0, 0, 0, 0.75);
}

.header__left {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  padding-right: 10px;
}

.header__input {
  display: flex;
  align-items: center;
  background-color: #eff2ef;
  padding: 15px;
  color: black;
  margin-left: 10px;
  border-radius: 999px;
}

.header__left > img {
  height: 50px;
}

.header__input > input {
  border: none;
  background-color: transparent;
  outline-width: 0;
}

.header__center {
  display: flex;
  flex: 1;
  justify-content: center;
}

.header__option > .MuiSvgIcon-root {
  color: rgba(0, 0, 0, 0.6);
}

.header__option:hover .MuiSvgIcon-root {
  color: #2e81f4;
}

.header__option--active > .MuiSvgIcon-root {
  color: #2e81f4;
}

.header__option--active {
  border-bottom: 4px solid #2e81f4;
}

.header__option {
  display: flex;
  align-items: center;
  padding: 0 30px;
  cursor: pointer;
}

.header__option:hover {
  background-color: #eff2ef;
  border-radius: 10px;
  align-items: center;
  padding: 0 30px;
  border-bottom: none;
}

.header__info {
  display: flex;
  align-items: center;
  padding: 15px;
}

.header__info > h4 {
  margin-left: 10px;
}

.header__right {
  display: flex;
}

.header__rightoption {
  border-radius: 999px;
  background-color: #e4e6eb;
  color: black;
  padding: 12px 10px 12px 10px;
}

@media only screen and (max-width: 600px) {
  .header {
    justify-content: space-between;
  }

  .header__center {
    display: none;
  }

  .header__right {
  }
}
